import { Server, Response } from "miragejs"
import categories from './data/categories'
import products from './data/products'

const itemBasicMapper = item => ({
  id: item.id,
  category: item.category,
  title: item.title,
  price: item.price,
  images: item.images,
});
const moreCount = 6;

export const makeServer = ({ environment = "development" } = {}) =>
{
  const topSalesId = [66, 65, 73];
  const topSales = products.filter(({id}) => topSalesId.includes(id));

  let server = new Server({
    environment,

    routes() {
      this.namespace = "api";

      this.get("/top-sales", () => JSON.stringify(topSales));
      this.get("/categories", () => JSON.stringify(categories));
      this.get("/items", (_, evt) =>
      {
        const { queryParams: query } = evt;

        const categoryId = query.categoryId === undefined ? 0 : Number(query.categoryId);
        const offset = query.offset === undefined ? 0 : Number(query.offset);
        const q = query.q === undefined ? '' : query.q.trim().toLowerCase();

        const filtered = products
          .filter(o => categoryId === 0 || o.category === categoryId)
          .filter(o => o.title.toLowerCase().includes(q) || o.color.toLowerCase() === q)
          .slice(offset, offset + moreCount)
          .map(itemBasicMapper);

        return JSON.stringify(filtered);
      });
      this.get("/items/:id", (_, evt) => products.find(({id}) => +id === +evt.params.id));
      this.post("/order", (_, evt) =>
      {
        const { owner: { phone, address }, items } = JSON.parse(evt.requestBody);

        if (typeof phone !== 'string') {
          return new Response(400, {}, {errors: ['Bad Request: Phone']});
        }
        if (typeof address !== 'string') {
          return new Response(400, {}, {errors: ['Bad Request: Address']});
        }
        if (!Array.isArray(items)) {
          return new Response(400, {}, {errors: ['Bad Request: Items']});
        }
        if (!items.every(({ id, price, count }) => {
          if (typeof id !== 'number' || id <= 0) {
            return false;
          }
          if (typeof price !== 'number' || price <= 0) {
            return false;
          }
          if (typeof count !== 'number' || count <= 0) {
            return false;
          }
          return true;
        })) {
          return new Response(400, {}, {errors: ['Bad Request']});
        }

        return new Response(204, {}, '');
      });
    },
  });

  return server;
};
