import React from 'react'
import Item from "./Item";

const ItemList = ({list}) =>
{
  const renderItems = data =>
  (
    data && data.map(({id, ...props}) => <Item key={id} id={id} {...props}/>)
  );

  return (
    <ul className='row'>
      {renderItems(list)}
    </ul>
  )
};

export default ItemList;
