import React, {useState, useRef, useEffect, useContext} from 'react';
import useFetch from "../../../hooks/useFetch";
import useLocalStorage from "../../../hooks/useLocalStorage";
import Loader from "../../../components/Loader";
import {ItemsCountContext} from "../../../context/ItemsCount";

const OrderTable = ({setSuccessConfirmOrder}) =>
{
  const [phone, setPhone] = useState('');
  const [address, setAddress] = useState('');
  const [agreement, setAgreement] = useState('');
  const addressInput = useRef(null);
  const phoneInput = useRef(null);
  const agreementInput = useRef(null);
  const [data, setData] = useLocalStorage('goods');
  const [{response, isLoading}, doFetch] = useFetch('/order');
  const [, setItemsCount] = useContext(ItemsCountContext);

  const onPhoneChange = value =>
  {
    if (value.length > 11) return;
    value = value.replace(/\D/g, '');
    setPhone(value);
  };

  useEffect(() =>
  {
    const validMessages = {
      phone: 'Пожалуйста введите 11 цифр',
      address: 'Пожалуйста введите адрес (более 10 символов)',
      agreement: 'Без вашего согласия мы не можем отправить заказ :('
    };

    [phoneInput, addressInput, agreementInput].forEach(({current}) =>
    {
      if (!current.value && !current.checked)
        current.setCustomValidity(validMessages[current.id]);
      else
        current.setCustomValidity('');
    });

  }, [address, agreement, phone]);

  useEffect(() =>
  {
    response === '' &&
      setSuccessConfirmOrder(true);
  }, [response, setSuccessConfirmOrder]);

  if (isLoading)
    return <Loader/>;


  const onFormSubmit = evt =>
  {
    evt.preventDefault();
    doFetch({
      data: JSON.stringify({
        owner: {
          phone, address
        },
        items: JSON.parse(data).map(({id, price, count}) => ({
          id, price, count
        }))
      }),
      method: 'post',
      headers: {
        'Content-Type': 'application/json'
      },
    });

    setData('');
    setItemsCount(0);
  };

  return (
    <div className="card"
         style={{maxWidth: '30rem', margin: '0 auto'}}>
      <form className="card-body" onSubmit={onFormSubmit}>
        <div className="form-group">
          <label htmlFor="phone">Телефон</label>
          <input type='tel' className="form-control"
                 id="phone"
                 ref={phoneInput}
                 pattern='[0-9]{11}'
                 placeholder="Ваш телефон" value={phone} onChange={evt => onPhoneChange(evt.target.value)}/>
        </div>
        <div className="form-group">
          <label htmlFor="address">Адрес доставки</label>
          <input className="form-control"
                 pattern='[0-9\.\w\s]{10,}'
                 id="address"
                 ref={addressInput}
                 onChange={evt => setAddress(evt.target.value)}
                 placeholder="Адрес доставки" value={address}/>
        </div>
        <div className="form-group form-check">
          <input type="checkbox"
                 ref={agreementInput}
                 className="form-check-input"
                 value=''
                 onChange={evt => setAgreement(evt.target.checked)}
                 id="agreement" checked={agreement}/>
          <label className="form-check-label"
                 htmlFor="agreement">Согласен с правилами доставки</label>
        </div>
        <button type="submit"
                className="btn btn-outline-secondary">Оформить
        </button>
      </form>
    </div>
  )
};

export default OrderTable;
