import React, {useEffect, useState, useReducer} from 'react'

import Header from "../../components/Header";
import Categories from "./components/Categories";
import ItemList from "../../components/ItemList";
import useFetch from "../../hooks/useFetch";
import Loader from "../../components/Loader";
import Search from "./components/Search";
import {initialState, reducer} from "../../reducer/itemLoading";
import LoadMore from "./components/LoadMore";

const Catalog = ({hasSearch = true, history}) =>
{
  const [query, setQuery] = useState('');
  const [operation, setOperation] = useState('');
  const [offset, setOffset] = useState('0');
  const [categoryId, setCategoryId] = useState('');
  const apiUrl = `/items?offset=${offset}&categoryId=${categoryId}&q=${query}`;
  const [{response, isLoading, error}, doFetch] = useFetch(apiUrl);
  const [{data}, setData] = useReducer(reducer, initialState);
  const [isLoadMoreAvailable, setIsLoadMoreAvailable] = useState(true);
  const searchParam = history && history.location.search && history.location.search.split('?')[1];

  const onLoadMoreClick = () =>
  {
    setOffset(state => String(+state + 6));
    setOperation('MORE_LOADED');
  };
  const onCategoriesChange = id =>
  {
    setOffset('0');
    setCategoryId(id);
    setOperation('LOADED');
  };
  const onSearchData = value =>
  {
    setOffset('0');
    setQuery(value);
    setOperation('LOADED');
  };

  useEffect(() =>
  {
    hasSearch && searchParam && setQuery(searchParam);
  }, [searchParam, hasSearch]);
  useEffect(() =>
  {
    setIsLoadMoreAvailable(false);
    doFetch();
  }, [apiUrl, doFetch]);

  useEffect(() =>
  {
    operation ?
      response &&
        setData({type: operation, payload: response})
      :
      setOperation('LOADED');

    setIsLoadMoreAvailable(() => !!(response && response[0] && response.length === 6));
  }, [response, operation]);

  if (error)
    return <div>Something goes wrong</div>;

  return (
    <section className='catalog'>
      <Header text='Каталог'/>
      {hasSearch && <Search onChange={onSearchData} value={query}/>}
      <Categories onChange={onCategoriesChange}/>
      {query && response && !response[0] &&
        <div>Ничего не найдено :(</div>}
      {response &&
        <>
          <ItemList list={data}/>
          {isLoadMoreAvailable && <LoadMore onClick={onLoadMoreClick}/>}
        </>}
      {isLoading && <Loader/>}
    </section>
  )
};


export default Catalog;
