import React, {useState, useContext, useRef} from 'react'
import {Link, NavLink} from "react-router-dom"
import {useHistory} from "react-router-dom"

import {ItemsCountContext} from "../context/ItemsCount"
import headerLogo from './../assets/img/header-logo.png'
import banner from './../assets/img/banner.jpg'

const TopBar = () => {
  const history = useHistory();
  const [query, setQuery] = useState('');
  const [invisible, setInvisible] = useState(true);
  const searchInput = useRef(null);
  const [count] = useContext(ItemsCountContext);

  const onChange = value => {
    setQuery(value);
  };
  const onFormSubmit = evt => {
    evt && evt.preventDefault();
    history.push({
      pathname: '/netologyRA/catalog.html',
      search: query,
    });
    setQuery('');
    setInvisible(true);
  };
  const onBlur = () => !query && setInvisible(true);
  const onClick = () => {
    !invisible ?
      query ? onFormSubmit() : setInvisible(state => !state)
      :
      setInvisible(state => !state);
  };
  const onCartClick = () => history.push('/netologyRA/cart.html');

  return (
    <header className="container">
      <div className="row">
        <div className="col">
          <nav className="navbar navbar-expand-sm navbar-light bg-light">
            <Link className="navbar-brand"
               to="/netologyRA/">
              <img src={headerLogo}
                   alt="Bosa Noga"/>
            </Link>

            <div className="collapase navbar-collapse"
                 id="navbarMain">
              <ul className="navbar-nav mr-auto">
                <li className="nav-item">
                  <NavLink className="nav-link"
                           to="/netologyRA/"
                           exact>Главная</NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link"
                           to="/netologyRA/catalog.html"
                           exact>Каталог</NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link"
                           to="/netologyRA/about.html"
                           exact>О магазине</NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link"
                           to="/netologyRA/contacts.html"
                           exact>Контакты</NavLink>
                </li>
              </ul>
              <div>
                <div className="header-controls-pics">
                  <div data-id="search-expander"
                       className="header-controls-pic header-controls-search"
                       onClick={onClick}/>
                  <div className="header-controls-pic header-controls-cart"
                       onClick={onCartClick}>
                    {!!count &&
                    <div className="header-controls-cart-full">{count}</div>}
                    <div className="header-controls-cart-menu"/>
                  </div>
                </div>
                <form data-id="search-form"
                      className={`header-controls-search-form form-inline${invisible && ' invisible'}`}
                      onSubmit={onFormSubmit}>
                  <input className="form-control"
                         placeholder="Поиск"
                         value={query}
                         ref={searchInput}
                         onBlur={onBlur}
                         onChange={evt => onChange(evt.target.value)}/>
                </form>
              </div>
            </div>
          </nav>
          <div className="banner">
            <img src={banner}
                 className="img-fluid"
                 alt="К весне готовы!"/>
            <h2 className="banner-header">К весне готовы!</h2>
          </div>
        </div>
      </div>
    </header>
  )
};

export default TopBar;
