import React, {useContext, useEffect} from 'react'
import useLocalStorage from "../../../hooks/useLocalStorage";
import {ItemsCountContext} from "../../../context/ItemsCount";

const Card = ({i, name, size, price, count, onClick, id}) =>
(
  <tr>
    <th scope="row">{i + 1}</th>
    <td><a href={`/netologyRA/products/${id}.html`}>{name}</a></td>
    <td>{size}</td>
    <td>{count}</td>
    <td>{price} руб.</td>
    <td>{price * count} руб.</td>
    <td>
      <button className="btn btn-outline-danger btn-sm"
              onClick={() => onClick(size, name)}>Удалить
      </button>
    </td>
  </tr>
);

const CardList = ({setIsBasketEmpty}) => {
  const [, setItemsCount] = useContext(ItemsCountContext);
  const [data, setData] = useLocalStorage('goods');
  let commonPrice = 0;
  const renderData = data && JSON.parse(data);

  useEffect(() =>
  {
    (!renderData || !renderData[0]) && setIsBasketEmpty(true);
  }, [renderData, setIsBasketEmpty]);

  const renderItems = () =>
  (
    renderData && renderData.map(({price, count, ...e}, index) => {
      commonPrice += price * count;
      const props = {
        ...e, onClick, price, count,
        i: index,
      };

      return <Card {...props} key={index}/>;
    })
  );
  const onClick = (size, name) =>
  {
    setData(state => {
      const newState = JSON.parse(state).filter(e => e.name !== name && e.size !== size);
      setItemsCount(newState.length);
      return JSON.stringify(newState);
    })
  };

  return (
    <table className="table table-bordered">
      <thead>
      <tr>
        <th scope="col">#</th>
        <th scope="col">Название</th>
        <th scope="col">Размер</th>
        <th scope="col">Кол-во</th>
        <th scope="col">Стоимость</th>
        <th scope="col">Итого</th>
        <th scope="col">Действия</th>
      </tr>
      </thead>
      <tbody>
        {renderItems()}
        <tr>
          <td colSpan="5"
              className="text-right">
            Общая стоимость
          </td>
          <td>{commonPrice.toLocaleString()} руб.</td>
        </tr>
      </tbody>
    </table>
  )
};

export default CardList;
