import React from "react";
import {Switch, Route} from 'react-router-dom';

import Home from "./pages/home/";
import Catalog from "./pages/catalog/";
import Contacts from "./pages/Contacts";
import About from "./pages/About";
import ItemFull from "./pages/ItemFull";
import Cart from "./pages/cart";
import NoMatch from "./pages/NoMatch";

export default () =>
(
  <Switch>
    <Route path='/netologyRA/' component={Home} exact/>
    <Route path='/netologyRA/catalog.html' component={Catalog}/>
    <Route path='/netologyRA/catalog/:slug.html' component={ItemFull}/>
    <Route path='/netologyRA/contacts.html' component={Contacts}/>
    <Route path='/netologyRA/about.html' component={About}/>
    <Route path='/netologyRA/cart.html' component={Cart}/>
    <Route path='*' component={NoMatch}/>
  </Switch>
);
