import React, {createContext, useState} from 'react';
import useLocalStorage from "../hooks/useLocalStorage";

export const ItemsCountContext = createContext([0, () => {}]);

export const ItemsCountProvider = ({children}) => {
  const [goods] = useLocalStorage('goods');
  const getGoodsLength = JSON.parse(goods || '[]').length;

  const [value, setValue] = useState( getGoodsLength);

  return (
    <ItemsCountContext.Provider value={[value, setValue]}>
      {children}
    </ItemsCountContext.Provider>
  );
};
