import React from 'react'

const Size = ({size, classNames, onClick}) =>
(
  <span className={classNames} onClick={() => onClick(size)}>{size}&nbsp;</span>
);

const SizesList = ({sizes, chooseSize, setChooseSize, count, setCount}) =>
{
  const renderSizes = () =>
  {
    return sizes.map(({size, available}) =>
    {
      if (available)
      {
        const props = {
          size,
          classNames: `catalog-item-size${chooseSize === size && ' selected'}`,
          onClick: size =>
          {
            setCount(1);
            setChooseSize(size);
          },
        };

        return <Size {...props} key={size}/>;
      }
      return false;
    })
  };
  const onSubAddClick = (add) =>
  {
    if (!chooseSize) return;

    return add ?
      setCount(count => count === 10 ? count : count + 1)
      :
      setCount(count => count === 1 ? count : count - 1);
  };

  return (
    <>
      <p>Размеры в наличии:&nbsp;
        {renderSizes()}
      </p>
      <p>Количество:
        <span className="btn-group btn-group-sm pl-2">
            <button className="btn btn-secondary" onClick={() => onSubAddClick(false)}>-</button>
            <span className="btn btn-outline-primary">{count}</span>
            <button className="btn btn-secondary" onClick={() => onSubAddClick(true)}>+</button>
        </span>
      </p>
    </>
  )
};

export default SizesList;
