import React, {useEffect, useState} from 'react'
import useFetch from "../../../hooks/useFetch";
import Loader from "../../../components/Loader";

const CategoriesItem = ({id, title, onClick, classNames}) =>
(
  <li className='nav-item'
      key={id}>
    <button className={classNames}
       onClick={evt => onClick(evt, id)}>{title}</button>
  </li>
);

const Categories = ({onChange}) => {
  const [{response, isLoading, error}, doFetch] = useFetch('/categories');
  const [active, setActive] = useState('0');

  const onItemClick = (evt, id) =>
  {
    evt.preventDefault();
    onChange(id);
    setActive(id);
  };

  useEffect(() => doFetch(), [doFetch]);

  if (error)
    return <div>Something goes wrong</div>;

  if (isLoading)
    return <Loader/>;

  return (
    <ul className='catalog-categories nav justify-content-center'>
      {response &&
      <>
        <CategoriesItem id='0'
                        onClick={onItemClick}
                        title='Все'
                        classNames={`nav-link ${'0' === active && 'active'}`}/>
        {response.map(({id, title}) => {
          const props = {
            id, title,
            onClick: onItemClick,
            classNames: `nav-link ${id === active && 'active'}`,
          };

          return (
            <CategoriesItem key={id}
                            {...props}/>)
        })}
      </>
      }
    </ul>
  )
};

export default Categories;
