import React, {useContext, useEffect, useState} from 'react'
import {Redirect} from 'react-router-dom'

import useFetch from "../../hooks/useFetch";
import Loader from "../../components/Loader";
import SizesList from "./components/SizesList";
import useLocalStorage from "../../hooks/useLocalStorage";
import {ItemsCountContext} from "../../context/ItemsCount";

const ItemFull = ({match}) => {
  const [, setItemsCount] = useContext(ItemsCountContext);
  const {slug} = match.params;
  const [{response, isLoading, error}, doFetch] = useFetch(`/items/${slug}`);
  const [chooseSize, setChooseSize] = useState(false);
  const [count, setCount] = useState(1);
  const [, setGoods] = useLocalStorage('goods');
  const [redirectToCart, setRedirectToCart] = useState(false);

  const onCartClick = () =>
  {
    setGoods(state =>
    {
      const newGood = {
        count, price, id,
        name: title,
        size: chooseSize,
      };

      if (state && state[0])
      {
        state = JSON.parse(state);
        const isGoodExists = state.find(e => {
          if (e.name === title && e.size === chooseSize)
          {
            e.count += count;
            return e;
          }
          return false;
        });

        isGoodExists || state.push(newGood);
        setItemsCount(state.length);
        return JSON.stringify(state);
      }
      else
      {
        setItemsCount(1);
        return JSON.stringify([newGood]);
      }
    });
    setRedirectToCart(true);
  };

  useEffect(() => doFetch(), [doFetch]);

  if (redirectToCart)
    return <Redirect to='/netologyRA/cart.html'/>;

  if (error)
    return <div>Something goes wrong</div>;

  if (isLoading || !response)
    return <Loader/>;

  const {title = '', images = ['no-photo.jpg'], sku = '', manufacturer = '', color = '', material = '', season = '', reason = '', sizes = [], price, id} = response;

  return (
    <section className="catalog-item">
      <h2 className="text-center">{title}</h2>
      <div className="row">
        <div className="col-5">
          <img src={images[0]}
               className="img-fluid"
               alt=""/>
        </div>
        <div className="col-7">
          <table className="table table-bordered">
            <tbody>
            <tr>
              <td>Артикул</td>
              <td>{sku}</td>
            </tr>
            <tr>
              <td>Производитель</td>
              <td>{manufacturer}</td>
            </tr>
            <tr>
              <td>Цвет</td>
              <td>{color}</td>
            </tr>
            <tr>
              <td>Материалы</td>
              <td>{material}</td>
            </tr>
            <tr>
              <td>Сезон</td>
              <td>{season}</td>
            </tr>
            <tr>
              <td>Повод</td>
              <td>{reason}</td>
            </tr>
            </tbody>
          </table>
          {sizes[0] &&
          <>
            <div className="text-center">
              <SizesList sizes={sizes}
                         chooseSize={chooseSize}
                         setChooseSize={setChooseSize}
                         count={count}
                         setCount={setCount}/>
            </div>
            <button onClick={onCartClick}
                    className="btn btn-danger btn-block btn-lg"
                    disabled={!chooseSize}>
              В корзину
            </button>
          </>}
        </div>
      </div>
    </section>
  )
};

export default ItemFull;
