export const initialState = {
  isLoading: false,
  data: [],
};

export const reducer = (state, action) =>
{
  switch(action.type)
  {
    case 'LOADING':
      return {...state, isLoading: true};
    case 'LOADED':
      return {isLoading: false, data: action.payload};
    case 'MORE_LOADED':
      return {
        ...state,
        isLoading: false,
        data: [...state.data, ...action.payload],
      };
    default:
      return state;
  }
};
