import React, {useState} from 'react'
import CardList from "./components/CardList";
import OrderTable from "./components/OrderTable";

const Cart = () => {
  const [isBasketEmpty, setIsBasketEmpty] = useState(false);
  const [successConfirmOrder, setSuccessConfirmOrder] = useState(false);

  if (isBasketEmpty)
    return <div>Nothing in basket found :(</div>;

  if (successConfirmOrder)
    return <div>Order send!</div>;

  return (
    <>
      <section className="cart">
        <h2 className="text-center">Корзина</h2>
        <CardList setIsBasketEmpty={setIsBasketEmpty}/>
      </section>
      <section className="order">
        <h2 className="text-center"> Оформить заказ</h2>
        <OrderTable setSuccessConfirmOrder={setSuccessConfirmOrder}/>
      </section>
    </>
  )
};

export default Cart;
